<template>
  <div class="vue-easymde">
    <textarea :name="name"></textarea>
  </div>
</template>

<script>
// eslint-disable-next-line import/extensions
import EasyMDE from 'easymde';
// eslint-disable-next-line import/extensions
import marked from 'marked';

export default {
  name: 'vue-easymde',
  props: {
    value: String,
    name: String,
    previewClass: String,
    autoinit: {
      type: Boolean,
      default() {
        return true;
      },
    },
    highlight: {
      type: Boolean,
      default() {
        return false;
      },
    },
    sanitize: {
      type: Boolean,
      default() {
        return false;
      },
    },
    configs: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  mounted() {
    if (this.autoinit) this.initialize();
  },
  activated() {
    const editor = this.easymde;
    if (!editor) return;
    const isActive = editor.isSideBySideActive() || editor.isPreviewActive();
    if (isActive) editor.toggleFullScreen();
  },
  methods: {
    initialize() {
      const configs = Object.assign({
        element: this.$el.firstElementChild,
        initialValue: this.value,
        renderingConfig: {},
      }, this.configs);

      // åæ­¥ value å initialValue çå¼ \ Synchronize the values of value and initialValue
      if (configs.initialValue) {
        this.$emit('input', configs.initialValue);
      }

      // å¤æ­æ¯å¦å¼å¯ä»£ç é«äº® \ Determine whether to enable code highlighting
      if (this.highlight) {
        configs.renderingConfig.codeSyntaxHighlighting = true;
      }

      // è®¾ç½®æ¯å¦æ¸²æè¾å¥çhtml \ Set whether to render the input html
      marked.setOptions({ sanitize: this.sanitize });

      // å®ä¾åç¼è¾å¨ \ Instantiated editor
      this.easymde = new EasyMDE(configs);

      // æ·»å èªå®ä¹ previewClass \ Add a custom previewClass
      const className = this.previewClass || '';
      this.addPreviewClass(className);

      // ç»å®äºä»¶ \ Binding event
      this.bindingEvents();
    },
    bindingEvents() {
      this.easymde.codemirror.on('change', () => {
        this.$emit('input', this.easymde.value());
      });
    },
    addPreviewClass(className) {
      const wrapper = this.easymde.codemirror.getWrapperElement();
      const preview = document.createElement('div');
      wrapper.nextSibling.className += ` ${className}`;
      preview.className = `editor-preview ${className}`;
      wrapper.appendChild(preview);
    },
  },
  destroyed() {
    this.easymde = null;
  },
  watch: {
    value(val) {
      if (val === this.easymde.value()) return;
      this.easymde.value(val);
    },
  },
};
</script>

<style>
  .vue-easymde .markdown-body {
    padding: 0.5em
  }

  .vue-easymde .editor-preview-active, .vue-easymde .editor-preview-active-side {
    display: block;
  }
</style>
